.fuellOfenSeiteFiexedTop {
    background-color: antiquewhite;
    padding: 0.25em 0;
    text-align: center;
    position: -webkit-sticky; /* Fest an der Kante beim scrollen anheften */
    position: sticky;
    top: 1px;
    z-index: 99;
}
#header_fixed.sticky
{
    position: fixed;
    background-color: antiquewhite;
    height: 50px;
    z-index: 99;
    /* Element wird aus Textfluss genommen, also mit diesen Werten arbeiten */
    top: 0px;
    left: 0px;
    right: 0px;
    
}

