
.panel_rr-panel-body
	{
		display: block;
		position: absolute;
		text-align: center;
		background-color: #eaeaea;
		height: auto;
		width: auto;
		border:1px solid #333333;
 		border-radius:0px;
		top:0;
		right:0;
		bottom:0;
		left:0;
	}

	.panel_rr-panel-footer
	{
		height:0px;
		background-color:#CCCCCC;
		position: absolute;
		bottom: 0;
		width: 100%;
		border-color: #CCCCCC;
		border-top-width: 1px;
		border-right-width: 0px;
		border-bottom-width: 0px;
		border-left-width: 0px;
		border-style: solid;
		border-radius:0 0 0px 0px;

	}
	
	.panel_rr-panel-header
	{
		height:20px;
		background-color:#87CEFA;
		position: absolute;
		top: 0;
		width: 100%; 
		border-color: #CCCCCC;
		border-top-width: 0px;
		border-right-width: 0px;
		border-bottom-width: 1px;
		border-left-width: 0px;
		border-style: solid;
		border-radius:0px 0px 0 0;
	}

	.panel_rr-header-gloss
	{
		background: -moz-linear-gradient(top,  hsla(0,0%,100%,0.3) 0%, hsla(0,0%,100%,0.5) 49%, hsla(0,0%,100%,0.7) 51%, hsla(0,0%,100%,0.4) 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(top,  hsla(0,0%,100%,0.3) 0%,hsla(0,0%,100%,0.5) 49%,hsla(0,0%,100%,0.7) 51%,hsla(0,0%,100%,0.4) 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to bottom,  hsla(0,0%,100%,0.3) 0%,hsla(0,0%,100%,0.5) 49%,hsla(0,0%,100%,0.7) 51%,hsla(0,0%,100%,0.4) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4dffffff', endColorstr='#66ffffff',GradientType=0 ); /* IE6-9 */
		height:100%;
		width: 100%; 
		opacity:0.1;
		border-radius:0px 0px 0 0;
	}
	.panel_rr-header-text {
    		font-family: Arial;
    		font-weight: normal;
    		font-size: 14px;
    		color: #333333;
    		position: absolute;
    		top: 0;
    		right: 0;
   	 	bottom: 0;
    		left: 0;
    		padding-top: 3px;
    		padding-right: 5px;
    		padding-bottom: 5px;
    		padding-left: 5px;
    		text-align: left;
    		overflow: hidden;
    		text-overflow: ellipsis;
	}
