.myTankRahmen {
    position: absolute;
    z-index: 99;
    cursor: pointer;
    border: 0px solid #A9A9A9;
}

    .myTankRahmen:hover {
        cursor: pointer;
        border: 3px solid #A9A9A9;
        -webkit-box-shadow: 0px 0px 30px 3px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 0px 30px 3px rgba(0,0,0,0.75);
        box-shadow: 0px 0px 30px 3px rgba(0,0,0,0.75);
    }
