.ccw-wf-pump0-handAni {
  visibility: visible;
	
	animation-name: flashHand;
	animation-duration: 1.5s;
	animation-timing-function: linear;
	animation-iteration-count: infinite;

	/*Firefox 1+*/
	-webkit-animation-name: flashHand;
