.ccw-wf-motor5-antriebAus {
	fill: #c0c0c0;  /* grau */
}

.ccw-wf-motor5-antriebEin {
  fill: #00ff00;  /* gruen */
}

.ccw-wf-motor5-antriebHand {
  fill: #ffff00;  /* gelb */
}

.ccw-wf-motor5-antriebHandEin {
  fill: #0000ff;  /* blau */
}

.ccw-wf-motor5-antriebStoer {
  fill: #ff0000;  /* rot */
}

.ccw-wf-motor5-antriebHochlauf {
  fill: #00ff00;  /* gruen */
	
	animation-name: flash;
	animation-duration: 0.5s;
	animation-timing-function: linear;
	animation-iteration-count: infinite;

	/*Firefox 1+*/
	-webkit-animation-name: flash;
	-webkit-animation-duration: 0.5s;
	-webkit-animation-timing-function: linear;
	-webkit-animation-iteration-count: infinite;

	/*Safari 3-4*/
	-moz-animation-name: flash;
	-moz-animation-duration: 0.5s;
	-moz-animation-timing-function: linear;
	-moz-animation-iteration-count: infinite;
}

.ccw-wf-motor5-antriebHochlaufHand {
  fill: #0000ff;  /* blau */
	
	animation-name: flash;
	animation-duration: 0.5s;
	animation-timing-function: linear;
	animation-iteration-count: infinite;

	/*Firefox 1+*/
	-webkit-animation-name: flash;
	-webkit-animation-duration: 0.5s;
	-webkit-animation-timing-function: linear;
	-webkit-animation-iteration-count: infinite;

	/*Safari 3-4*/
	-moz-animation-name: flash;
	-moz-animation-duration: 0.5s;
	-moz-animation-timing-function: linear;
	-moz-animation-iteration-count: infinite;
}

.ccw-wf-motor5-antriebStoerNeu {
  fill: #ff0000;  /* rot */
	
	animation-name: flash;
	animation-duration: 0.5s;
	animation-timing-function: linear;
	animation-iteration-count: infinite;

	/*Firefox 1+*/
	-webkit-animation-name: flash;
	-webkit-animation-duration: 0.5s;
	-webkit-animation-timing-function: linear;
	-webkit-animation-iteration-count: infinite;

	/*Safari 3-4*/
	-moz-animation-name: flash;
	-moz-animation-duration: 0.5s;
	-moz-animation-timing-function: linear;
	-moz-animation-iteration-count: infinite;
}

.ccw-wf-motor5-handAni {
  visibility: visible;
	
	animation-name: flashHand;
	animation-duration: 1.5s;
	animation-timing-function: linear;
	animation-iteration-count: infinite;

	/*Firefox 1+*/
	-webkit-animation-name: flashHand;
	-webkit-animation-duration: 1.5s;
	-webkit-animation-timing-function: linear;
	-webkit-animation-iteration-count: infinite;

	/*Safari 3-4*/
	-moz-animation-name: flashHand;
	-moz-animation-duration: 1.5s;
	-moz-animation-timing-function: linear;
	-moz-animation-iteration-count: infinite;
}

@keyframes flash {  
  0% { opacity: 1.0; }
  50% { opacity: 0.0; }
	100% { opacity: 1.0; }
}

/*Firefox 1+*/
@-webkit-keyframes flash {  
  0% { opacity: 1.0; }
  50% { opacity: 0.0; }
  100% { opacity: 1.0; }
}

/*Safari 3-4*/
@-moz-keyframes flash {  
  0% { opacity: 1.0; }
  50% { opacity: 0.0; }
  100% { opacity: 1.0; }
}

@keyframes flashHand {  
	0% { opacity: 1.0; }
	25% { opacity: 0.5; }
  50% { opacity: 0.0; }
	75% { opacity: 0.5; }
	100% { opacity: 1.0; }
}

/*Firefox 1+*/
@-webkit-keyframes flashHand {  
	0% { opacity: 1.0; }
	25% { opacity: 0.5; }
  50% { opacity: 0.0; }
	75% { opacity: 0.5; }
	100% { opacity: 1.0; }
}

/*Safari 3-4*/
@-moz-keyframes flashHand {  
	0% { opacity: 1.0; }
	25% { opacity: 0.5; }
  50% { opacity: 0.0; }
	75% { opacity: 0.5; }
	100% { opacity: 1.0; }
}